import React from "react";
import HomePresenter from "./HomePresenter";

export default class extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      changeDay: 0
      // changeDayOfWeek: "ì›”"
    };
  }

  prevButton = () => {
    this.setState(state => ({
      changeDay: parseInt((state.changeDay - 1 + 3) % 3)
    }));
    //   if(this.state.changeDayOfWeek.day === "ì›”"){
    //     this.state.changeDayOfWeek.day = "ìˆ˜";
    //   }else if(this.state.changeDayOfWeek === "í™”"){
    //     this.state.changeDayOfWeek.day = "ì›”";
    // }else{
    //   this.state.changeDayOfWeek.day = "í™”";
    // }
  };

  nextButton = () => {
    this.setState(state => ({
      changeDay: parseInt((state.changeDay + 1) % 3)
      // changeDayOfWeek: (state.changeDayOfWeek = state.changeDayOfWeek[2])
    }));
  };

  render() {
    const {
      changeDay
      // changeDayOfWeek
    } = this.state;
    return (
      <HomePresenter
        changeDay={changeDay}
        prevButton={this.prevButton}
        nextButton={this.nextButton}
        // changeDayOfWeek={changeDayOfWeek}
      />
    );
  }
}
